@import url("https://fonts.googleapis.com/css2?family=Playfair+Display&family=Quicksand:wght@300&display=swap");
html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-family: "Quicksand", "Helvetica Neue", sans-serif;
  background-color: #e2e4de;
  color: #434341;
}

/* code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
} */

.fancy-font {
  font-family: "Playfair Display", "Helvetica Neue", sans-serif;
  font-size: 3em;
}

#main,
#about,
#projects,
#poetry {
  padding: 5em;
  height: 750px;
}

#main {
  display: flex;
  align-items: center;
}

#about {
  display: flex;
  justify-content: space-evenly;
  padding-top: 8em;
  height: 600px;
}

#about > img {
  height: 400px;
  border-radius: 300px;
  margin: 2em;
}

#poetry {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.title {
  display: flex;
  width: 10em;
  font-size: 1.5em;
}

.line {
  width: 3em;
  height: 1px;
  background-color: #434341;
  border: none;
  transform: translateY(0.2em);
}

.navbar {
  display: flex;
  justify-content: space-between;
  overflow: hidden;
  position: fixed;
  top: 0;
  width: 100%;
  padding: 0.7em;
}

.navbar a {
  text-decoration: none;
  color: #434341;
}

.navbarLinks {
  display: flex;
  justify-content: space-evenly;
  width: 20em;
}
/* 
