  transition: all 0.2s ease-in-out;
}

.navbarLinks > a:hover {
  transform: scaleY(1.2);
} */

.chinese {
  display: flex;
  flex-direction: column;
}

#poetryPage {
  border: 1px solid #434341;
  width: 40em;
  height: 40em;
  padding: 4em 0 0 4em;
}

.linkButton {
  text-decoration: none;
  border: 1px solid #434341;
  padding: 0.5em 1em;
  border-radius: 2em;
  color: #434341;
}

.linkButton:hover {
  background-color: rgb(255, 255, 255);
}

.contactLinks {
  display: flex;
  align-items: center;
  overflow: hidden;
  position: fixed;
  left: 1em;
  height: 100%;
  width: 25px;
  writing-mode: vertical-rl;
  text-orientation: mixed;
  transform: translateY(50%);
}

.socialIcon {
  width: 20px;
  margin: 0.5em 0 0.5em 0;
  transition: all 0.2s ease-in-out;
}

.socialIcon:hover {
  transform: scale(1.2);
}

footer {
  padding: 1em;
}
